<template>
  <div class="content">
    <span class="md-headline">{{ name }}</span>
    <router-view />
    <Sidebar />
    <md-snackbar :md-active="!!alert" :md-closed="() => (alert = false)">
      <span>{{ alert }}</span>
    </md-snackbar>
  </div>
</template>

<script>
import Sidebar from "@/components/Sidebar.vue";

export default {
  data: () => ({
    alert: false
  }),
  methods: {
    catchError(err) {
      switch (err.code) {
        case 4001:
          this.alert = "Не удалось понять запрос";
          break;
        case 4002:
          this.alert = "Запрос построен неверно";
          break;
        case 4004:
          this.alert = "Неизвестный метод";
          break;
        case 4003:
        case 4005:
          this.alert = err.msg;
          break;
        case 4006:
          this.alert = "Ошибка парсера, попробуйте еще раз";
          break;
        case 4007:
          this.alert = "Для этого метода нужна авторизация в сетевом";
          break;
        case 4008:
          this.alert = "Сетевой город не отвечает";
          break;
        case 4009:
          this.$router.replace("login");
          this.alert = "Для начала нужно войти в систему";
          break;
        default:
          this.alert = "Неизвестная ошибка, попробуйте ещё раз";
      }
      console.error(err);
    }
  },
  computed: {
    name() {
      return this.$route.name;
    }
  },
  beforeCreate() {
    fetch(
      `//${process.env.VUE_APP_host || location.host}/exist/${localStorage.uid}`
    )
      .then(res => res.json())
      .then(exist => {
        if (!exist) this.$router.replace("login");
      })
      .catch(() => this.$router.replace("login"));
  },
  mounted() {
    this.$root.$on("catch-error", this.catchError);
  },
  components: {
    Sidebar
  }
};
</script>

<style scoped>
.content {
  margin-bottom: 56px;
  padding-top: 0.5rem;
  padding-left: 0.5rem;
}
.md-snackbar {
  z-index: 1001;
}
</style>
