export default {
  state: {
    storage: JSON.parse(localStorage["vi-sgo-cache"] || "{}")
  },
  actions: {
    getCache({ state }, data) {
      if (!("name" in data) || !("next" in data)) return false;
      if (
        Date.now() - state.storage[data.name]?.timestamp <
        (data.time || 6e4)
      ) {
        return state.storage[data.name].data;
      } else return data.next();
    },
    saveCache({ commit }, data) {
      if (!("name" in data) || !("value" in data)) return false;
      commit("changeCacheStorage", {
        name: data.name,
        value: data.value
      });
      return data.value;
    }
  },
  mutations: {
    changeCacheStorage(state, data) {
      state.storage[data.name] = {
        data: data.value,
        timestamp: Date.now()
      };
      localStorage["vi-sgo-cache"] = JSON.stringify(state.storage);
    }
  }
};
