export default {
  state: {
    storage: JSON.parse(localStorage["vi-sgo-settings"] || "{}")
  },
  actions: {
    saveSettings({ commit }, data) {
      if (!("name" in data) || !("value" in data)) return false;
      commit("changeSettingsStorage", {
        name: data.name,
        value: data.value
      });
      return true;
    }
  },
  mutations: {
    changeSettingsStorage(state, data) {
      state.storage[data.name] = data.value;
      localStorage["vi-sgo-settings"] = JSON.stringify(state.storage);
    }
  }
};
